<template>
  <v-container>
    <div v-if="currentBranch" class="edit-form py-3">
      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT BRANCH</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addBranch"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD BRANCH</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/branches"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >BRANCH LIST</v-btn
          >
        </v-col>
      </v-row>

      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentBranch.name"
          :rules="[(v) => !!v || 'Branch Name is required']"
          label="Branch Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentBranch.brCode"
          :rules="[(v) => !!v || 'Branch Code is required']"
          label="Branch Code"
          required
        ></v-text-field>

        <v-divider class="my-5"></v-divider>

        <v-btn color="error" small class="mr-2" @click="deleteBranch">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateBranch"> Update </v-btn>
      </v-form>

      <p class="mt-3">{{ message }}</p>
    </div>

    <div v-else>
      <p>Please click on a Branch...</p>
    </div>
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";

export default {
  name: "branch",
  data() {
    return {
      currentBranch: null,
      message: "",
    };
  },
  methods: {
    getBranch(id) {
      BranchDataService.get(id)
        .then((response) => {
          this.currentBranch = response.data;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateBranch() {
      BranchDataService.update(this.currentBranch.id, this.currentBranch)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "branches" });
          // console.log(response.data);
          // this.message = "The Branch was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteBranch() {
      BranchDataService.delete(this.currentBranch.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "branches" });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.message = "";
    this.getBranch(this.$route.params.id);
  },
};
</script>
